<script>
    import $ from 'jquery'
    import Typed from 'typed.js'

    $(document).ready(function() {
        /** 
            * typed.js
        */
        var name_options = {
            strings: ["Molly Lin"],
            typeSpeed: 200
        }
        var intro_options = {
            strings: ["Hello! ^500  這是我的簡介"],
            typeSpeed: 200
        }

        var typed_name = new Typed("#name_type", name_options);
        var typed_intro = new Typed("#hello_header", intro_options);

        /** 
            * navbar scrollto animate
        */
       $(function() {
            $('a[href*="#"]:not([href="#"])').click(function() {
                if (location.pathname.replace(/^\//, '') == this.pathname.replace(/^\//, '') && location.hostname == this.hostname) {
                    var target = $(this.hash);
                    target = target.length ? target : $('[name=' + this.hash.slice(1) + ']');
                    if (target.length) {
                        $('html, body').animate({
                            scrollTop: target.offset().top - 60
                        }, 1000);
                        return false;
                    }
                }
            });
        });
    });
    
    
	export default {
		created () {
			console.log('home.vue')
        },
        methods: {
            gaEvent(eventCategory, eventLabel) {
                ga('send', {
                    hitType: 'event',
                    eventCategory: eventCategory,
                    eventAction: 'click',
                    eventLabel: eventLabel
                });
            } 
        }
	}
</script>

