            img {
                margin-top: 8px;
                object-fit: contain;
                max-height: 308px;
                border-radius: 50%;
            }
        }
        .card_right {
            h1 {
                font-size: 44px;
                display: inline-block;
                font-weight: bold;
            }
            .text_muted {
                font-size: 18px;
            }
            hr {
                margin: 25px -20px;
            }
            dl.dl_horizontal {
                margin-bottom: 24px;
                >* {
                    margin-bottom: 10px;
                }
                dt {
                    float: left;
                    clear: left;
                    color: #11958b;
                    width: 80px;
                    font-weight: 700;
                }
                dd {
                    margin-left: 80px;
                    a {
                        color: #343434;
                    }
                }
            }
            ul.list_inline {
                padding-left: 0;
                list-style-type: none;
                margin-left: -5px;
                >li {
                    display: inline-block;
                    a {
                        color: #c8c7c7;
                        display: inline-block;
                        margin: 3px 10px 3px 3px;
                        i {
                            background-color: #c8c7c7;
                            width: 35px;
                            padding: 5px;
                            font-size: 1.5em;
                            text-align: center;
                            color: #fff;
                            transition: all .15s linear;
                        }
                        &:hover {
                            i {
                                background-color: #11958b;
                            }
                        }
                    }
                }
            }
        }
    }
    .intro_text {
        margin-top: 65px;
        text-align: center;
        color: #343434;
        #hello_header {
            margin-bottom: 25px;
            display: inline-block;
        }
        p {
            line-height: 30px;
        }
        .download_btn {
            margin-top: 40px;
            .btn {
                color: #fff;
                background-color: #11958b;
                border: 3px solid transparent;
                border-radius: 0;
                padding: 6px 12px;
                transition: all .15s linear;
                &:hover {
                    background-color: transparent;
                    border: 3px solid #11958b;
                    color: #11958b;
                }
            }
        }
    }
}
.skill {
    margin: 65px 0;
    .skill_text {
        color: #343434;
        text-align: center;
        #skill_header {
            margin-bottom: 25px;
            display: inline-block;
        }
        h3.type {
            font-size: 20px;
            display: block;
            padding: 0 0 20px;
            border-bottom: 1px solid #e2e2e2;
            font-weight: bold;
            margin: 20px 0;
            color: #4a4a4a;
            position: relative;
            text-align: left;
            font-weight: 300;
            &:before {
                position: absolute;
                left: 20px;
                bottom: -3px;
                width: 50px;
                height: 5px;
                content: '';
                display: inline-block;
                background-color: #11958b;
            }
        }
        .skill_section {
            h4 {
                margin-bottom: 25px;
                color: #11958b;
            }
            >div {
                ul {
                    list-style-type: none;
                    text-align: left;
                    li {
                        line-height: 2;
                        i.fa {
                            padding-right: 10px;
                        }
                    }
                }
            }
            .skill-1 {
                animation-duration: 3s;
            }
            .skill-2 {
                animation-duration: 4s;
            }
            .skill-3 {
                animation-duration: 5s;
            }
        }
    }
}
.portfolio {
    margin: 65px 0;
    h2 {
        text-align: center;
        color: #343434;
        margin-bottom: 25px;
        margin-top: 20px;
        // display: inline-block;
    }
    .item_box {
        margin-bottom: 25px;
        .post_img {
            position: relative;
            width: 100%;
            height: 200px;
            overflow: hidden;
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                display: block;
            }
            span {
                position: absolute;
                top: 10px;
                left: 10px;
                padding: 5px 15px;
                background-color: #8b1195;
                color: #fff;
            }
        }
        .post_info {
            position: relative;
            text-align: center;
            padding: 25px;
            margin-top: 50px;
            border-left: 1px solid #b7dfdc;
            border-bottom: 1px solid #b7dfdc;
            height: 260px;
            overflow: hidden;
            .date {
                color: #707070;
                font-size: 13px;
                position: absolute;
                padding-left: 50px;
                top: -2px;
                left: 0;
                &:after {
                    width: 40px;
                    height: 20px;
                    color: red;
                    content: '';
                    position: absolute;
                    top: 10px;
                    left: 0;
                    border-top: 1px solid #b7dfdc;
                }
            }
            .post_title {
                font-size: 18px;
                letter-spacing: 1px;
                margin-top: 0;
                margin-bottom: 10px;
                color: #4a4a4a;
                font-weight: 300;
                text-align: justify;
                line-height: 1.5;
            }
            .post_text {
                margin-bottom: 10;
                color: #707070;
                font-weight: 100;
                text-align: justify;
            }
            ul.post_link_ul {
                list-style-type: none;
                position: absolute;
                bottom: 20px;
                li {
                    display: inline-block;
                    margin: 0 10px;
                    a {
                        color: #11958b;
                        transition: all .3s ease 0s;
                        font-weight: 300;
                        padding: 5px 10px;
                        border: 1px solid #11958b;
                        &:focus {
                            outline: none;
                        }
                        &:hover {
                            text-decoration: none;
                            background: #11958b;
                            color: #fff;
                        }
                    }
                }
            }
            >a {
                color: #11958b;
                transition: all .3s ease 0s;
                font-weight: 300;
                padding: 5px 10px;
                border: 1px solid transparent;
                position: absolute;
                bottom: 20px;
                left: 50%;
                margin-left: -18px;
                border: 1px solid #11958b;
                &:focus {
                    outline: none;
                }
                &:hover {
                    text-decoration: none;
                    background: #11958b;
                    color: #fff;
                }
            }
        }
    }
}
@media screen and (max-width: 768px) {
    .menu_wrapper {
        .menu_collapsed {
            nav {
                padding-left: 20px;
                text-align: center;
            }
        }
    }
}
@media screen and (max-width: 768px) {
    #front {
        background-size: 250%;
        .card .card_left {
            text-align: center;
            img {
                max-height: 200px;
            }
        }
        .card .card_right {
            text-align: center;
            h1 {
                font-size: 28px;
            }
            .text_muted {
                font-size: 14px;
            }
            hr {
                margin: 0;
            }
            dl.dl_horizontal {
                width: 300px;
                margin: 20px auto;
            }
            ul.list_inline {
                margin-left: 0;
            }
        }
        .intro_text {
            #hello_header {
                font-size: 22px;
            }
            p {
                text-align: left;
                font-size: 14px;
            }
        }
    }
}
@media screen and (max-width: 768px) {
    #skill {
        .skill_text {
            #skill_header {
                font-size: 22px;
            }
        }
    }    
}
@media screen and (max-width: 1200px) {
    .portfolio {
        .item_box {
            .post_img {
                height: 150px;
                ul.post_link_ul {
                    padding-left: 0;
                }
            }
            .post_info {
                height: 300px;
                ul.post_link_ul {
                    width: 100%;
                    padding: 0;
                    margin-left: -18px;
                }
                .post_title {
                    font-size: 16px;
                }
            }
        }
    }
}
@media screen and (max-width: 768px) {
    .portfolio {
        h2 {
            font-size: 22px;
        }
        .item_box {
            .post_img {
                height: 250px;
                img {
                    width: 447px;
                    margin: 0 auto;
                }
            }
        }
    }
}  
</style>


