import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Home from '@/components/Home.vue';

Vue.use(Router)

const routes = [{
        path: '/resume',
        name: 'home',
        component: Home
    },
    { path: '/*', redirect: '/resume' }
]

export default new Router({
    mode: 'history', //dev時打開，build前註解需後端配合不然build會有問題， 坑!
    base: '/',
    routes
})


